.base {
  position: absolute;
  top: 134px;
  left: 0;
  width: 1440px;
  height: 546px;
}
.oval-icon {
  position: relative;
  width: 1186px;
  height: 1190.4px;
  opacity: 0.15;
  mix-blend-mode: normal;
}
.right-icon {
  position: absolute;
  top: 201px;
  left: 98px;
  width: 456px;
  height: 466.3px;
  object-fit: contain;
}
.experience-and-integ {
  width: 99.82%;
  top: calc(50% - 132.5px);
  letter-spacing: 0.2px;
  line-height: 48px;
  color: var(--color-darkslategray-100);
  height: 48.2px;
}
.experience-and-integ,
.our-team,
.the-right-tools {
  position: absolute;
  left: 0.18%;
  display: inline-block;
}
.our-team {
  width: 15.47%;
  top: calc(50% - 167.6px);
  font-size: var(--body-small-size);
  letter-spacing: 2px;
  text-transform: uppercase;
  height: 18.1px;
}
.the-right-tools {
  width: 82.37%;
  top: calc(50% - 28.3px);
  font-size: var(--body-large-size);
  line-height: 32px;
  color: var(--color-black);
  height: 122.5px;
}
.fill1,
.shadow1 {
  height: calc(100% - 0.2px);
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0.2px;
  left: 0;
  border-radius: var(--br-1981xl);
}
.shadow1 {
  position: absolute;
}
.fill1 {
  border: 2px solid var(--color-tomato-100);
  box-sizing: border-box;
}
.fill1,
.layer,
.text {
  position: absolute;
}
.layer {
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-5xs);
  overflow: hidden;
}
.text {
  top: calc(50% - 11px);
  left: calc(50% - 54px);
}
.icon3 {
  top: calc(50% - 6px);
  left: calc(50% + 57px);
  font-size: var(--body-small-size);
  font-family: var(--font-font-awesome-5-free);
}
.button1,
.fix,
.icon3 {
  position: absolute;
}
.fix {
  width: calc(100% - 45px);
  top: calc(50% - 12.2px);
  right: 22.5px;
  left: 22.5px;
  border-radius: var(--br-5xs);
  height: 22px;
  overflow: hidden;
}
.button1 {
  height: calc(100% - 287px);
  width: calc(100% - 371px);
  top: 287px;
  right: 371px;
  bottom: 0;
  left: 0;
  text-align: center;
  font-size: var(--body-medium-size);
  font-family: var(--font-open-sans);
}
.vision {
  position: absolute;
  width: 38.61%;
  top: calc(50% - 103px);
  right: -2.5%;
  left: 63.89%;
  height: 335.2px;
}
.team {
  width: 100%;
  position: relative;
  height: 680px;
  text-align: left;
  font-size: var(--font-size-17xl);
  color: var(--color-tomato-100);
  font-family: var(--body-large);
}
